"use client";

import * as React from "react";
import { useLocale } from "next-intl";
import { useRouter, usePathname } from "next/navigation";
import Image from "next/image";
import { DropdownMenu, DropdownMenuTrigger, DropdownMenuContent, DropdownMenuItem } from "@/components/ui/dropdown-menu";
import { Button } from "@/components/ui/button";

function LanguageSwitcher() {
  const locale = useLocale();
  const router = useRouter();
  const pathname = usePathname();
  const [mounted, setMounted] = React.useState(false);

  React.useEffect(() => {
    setMounted(true);
  }, []);

  if (!mounted) return null;

  const switchLanguage = (newLocale) => {
    const pathSegments = pathname.split("/");
    if (pathSegments.length > 1) {
      pathSegments[1] = newLocale;
    } else {
      pathSegments.push(newLocale);
    }
    router.replace(pathSegments.join("/"), { scroll: false });
  };

  return (
    <div className="mt-1 relative z-[200]">
      <DropdownMenu>
        <DropdownMenuTrigger asChild>
          <Button variant="outline" className="flex items-center p-2 rounded text-gray-700 dark:text-white transition">
            <Image
              src={locale === "en" ? "/images/english.webp" : "/images/nepali.png"}
              alt={locale === "en" ? "English Flag" : "Nepali Flag"}
              className="transition-transform duration-200"
              width={20}
              height={20}
            />
            <span className="ml-2 text-sm">{locale === "en" ? "EN" : "NP"}</span>
          </Button>
        </DropdownMenuTrigger>
        <DropdownMenuContent className="z-[300]">
          <DropdownMenuItem onClick={() => switchLanguage("en")}>\
            <Image
              src="/images/english.webp"
              alt="English Flag"
              width={20}
              height={20}
              className="mr-2"
            />
            English
          </DropdownMenuItem>
          <DropdownMenuItem onClick={() => switchLanguage("np")}>\
            <Image
              src="/images/nepali.png"
              alt="Nepali Flag"
              width={20}
              height={20}
              className="mr-2"
            />
            Nepali
          </DropdownMenuItem>
        </DropdownMenuContent>
      </DropdownMenu>
    </div>
  );
}

export default LanguageSwitcher;
